.carousel-container {
    display: flex;
    overflow: hidden;
    width: 100%;
    white-space: nowrap;
}

.carousel-content {
    display: flex;
}

.carousel-image {
    width: auto;
    height: 300px;
    border-radius: 16px;
    margin: 10px;
}

.carousel-image:hover {
    cursor: pointer;
}

.carousel-text {
    font-family: "Montserrat", sans-serif;
    padding-left: 20px;
    margin-top: 0;
    color: white;
}

.modal-window-image {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.795);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: opacity 0.5 ease, visibility 0.5s ease;
    z-index: 1000;
}

.modal-text {
    font-family: "Funnel Sans", sans-serif;
    font-size: 25px;
    background: white;
    color: black;
    font-weight: 500;
    filter: drop-shadow(10px 10px 10px rgb(0, 0, 0));
    padding: 10px;
    border-radius: 10px;
    position: absolute;
    margin-left: 20px;
}

.modal-image-link-button {
    font-family: "Funnel Sans", sans-serif;
    padding-top: 20px;
    padding-bottom: 50px;
    font-size: 15px;
    margin-top: 60px;
    margin-left: 10px;
    position: absolute;
    width: 50px;
    height: auto;
    background: black;
    border: none;
    cursor: pointer;
    border-radius: 20px;
    color: white;
    outline: none;
}

.modal-image-link-button-icon {
    margin: 0px;
    position: absolute;
    bottom: 0px;
    right: 0px;
    content: url(/public/icons/arrow.png);
    background: none;
    height: 50px;
    width: 50px;
    filter: invert(100%);
}

.modal-image-link-button:hover {
    filter: invert(100%) drop-shadow(0px 0px 20px #ff2e5b);

}

.modal-image {
    max-width: 70vw;
    max-height: 70vh;
    border-radius: 10px;
}

.modal-image-close-button {
    margin-top: -18px;
    margin-left: 10px;
    position: absolute;
    width: 50px;
    height: 50px;
    background: black;
    border: none;
    cursor: pointer;
    border-radius: 50%;
    font-size: 0;
    outline: none;
}

.modal-image-close-button::before,
.modal-image-close-button::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 30px;
    height: 4px;
    background-color: white;
}

.modal-image-close-button::before {
    transform: translate(-50%, -50%) rotate(45deg);
}

.modal-image-close-button::after {
    transform: translate(-50%, -50%) rotate(-45deg);
}

.modal-image-close-button:hover {
    filter: drop-shadow(0px 0px 20px #ff2e5b);
    background: white;
}