.modal-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.791);
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.5s ease, visibility 0.5s ease;
    z-index: 1000;
}

.modal-backdrop.show {
    opacity: 1;
    visibility: visible;
}

.modal-content {
    font-family: "Montserrat", sans-serif;
    position: relative;
    background: rgb(24, 24, 24);
    padding: 40px;
    border-radius: 16px;
    width: 500px;
    height: 285px;
    transform: translateY(-50px);
    transition: transform 0.5s ease;
    border: 5px solid rgb(0, 0, 0);
    z-index: 1001;
}

.modal-backdrop.show .modal-content {
    transform: translateY(0);
}

.modal-close-button {
    position: absolute;
    color: white;
    border: none;
    cursor: pointer;
    font-size: 20px;
    font-weight: 1000;
    background: none;
    outline: none;
    top: 20px;
    right: 20px;
    padding: 0px;
    transition: .2s;
}

.modal-close-button:hover {
    color: #ff2e5b;
    filter: drop-shadow(0px 0px 3px #ff2e5b);
}

