.social-button {
    align-items: center;
    display: flex;
    height: 55px;
    width: 220px;
    border-radius: 55px;
    background: #000000;
    border: 2px solid #ff2e5b;
    cursor: pointer;
    transform: 0.3s;
    transition: 0.3s;
    margin-right: 2%;
    color: white;
    font-size: 20px;
    text-decoration: none;
}

.social.telegram {
    content: url(/public/icons/tg.png);
    height: 50px;
    margin-top: -2%;
    margin-bottom: -2%;
    background-color: white;
    border-radius: 50%;
    filter: invert(100%);
    margin-right: 5%;
    margin-left: 2%;
}

.social.gmail {
    content: url(/public/icons/mail.png);
    height: 50px;
    margin-top: -2%;
    margin-bottom: -2%;
    background-color: white;
    border-radius: 50%;
    filter: invert(100%);
    margin-right: 5%;
    margin-left: 2%;
}

.social-button:hover {
    filter: drop-shadow(0px 0px 10px #ff2e5b);
    scale: 1.05;
    color: rgb(255, 255, 255);
    filter: invert(100%);
}

.contact-text {
    color: white;
    font-size: 25px;
    margin-bottom: 30px;
}