.project-template {
    font-family: "Montserrat", sans-serif;
    ;
}


/* -------- HEADER BLOCK ---------- */

.contactButton.back {
    text-decoration: none;
    color: white;
    padding-left: 0;
    width: 800px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.contactButton.back p {
    text-align: center;
    margin: 0;
}

.contactButton-img {
    height: 60px;
    width: 60px;
    content: url(/public/icons/arrow.png);
    filter: invert(100%);
}


/* ------------ MAIN BLOCK ------------ */

.project-main {
    position: relative;
    color: white;
    font-family: "Montserrat", sans-serif;
    height: 130vh;
    background: url(/public/images/back1.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
}

.gradient {
    width: 100%;
    height: 30vh;
    position: absolute;
    bottom: 0;
    background: linear-gradient(to bottom, #00000000 0%, rgb(0, 0, 0) 100%);
}



.project-main-fade {
    background: rgba(0, 0, 0, 0.88);
    height: 100%;
    width: 100%;
}

.project-main-container {
    display: flex;
    flex-direction: row;
    gap: 8%;
    padding: 10% 15%;
}

.project-main-left,
.project-main-right {
    position: relative;
    width: 100%;
    height: 80vh;
    border: 1px solid black;
}

/*project-main-LEFT*/

.project-main-left {
    background: url(/public/images/code_back.jpeg);
    background-size: cover;
    background-size: 150%;
    filter: drop-shadow(10px 10px 0px rgba(193, 193, 193, 0.100));
}

.project-main-left-fade {
    position: relative;
    display: flex;
    text-align: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom, #ff2e5b00 20%, rgb(17, 17, 17) 100%);
    z-index: 1;
}

.project-main-left-back {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0.9;
}

.project-main-left-fade p {
    position: absolute;
    font-size: 1vw;
    left: 0;
    top: 0;
    color: rgb(0, 0, 0);
    background: #ffffffb7;
    border-radius: 10px;
    padding: 5px;
    font-family: "Noto Sans Mono", monospace;
    margin: 20px 20px;
    filter: drop-shadow(0px 0px 8px black);
}

.project-title-left {
    position: absolute;
    bottom: 5%;
}

.project-title-left h1 {
    font-size: 3vw;
    text-shadow:
        2px 2px 0 #000,
        -2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000;
    filter: drop-shadow(0px 0px 10px #ff2e5b);
}

.project-title-left h2 {
    font-size: 1.5vw;
    font-family: "Noto Sans Mono", monospace;
}

/*project-main-RIGHT*/

.project-main-right {
    background: #181818;
    background: url(/public/images/code_back_fade.jpeg);
    /* background: url(/public/images/code_back2.jpg); */
    background-size: cover;
    background-position-x: -50%;
    background-position-y: 0px;
    filter: drop-shadow(10px 10px 0px rgba(193, 193, 193, 0.100));
}

.project-main-right-fade {
    padding: 0px 10%;
    position: relative;
    display: flex;
    text-align: left;
    justify-content: center;
    width: 80%;
    height: 100%;
    background: linear-gradient(to bottom, #ff2e5b00 30%, #ff2e5b 100%);
    z-index: 1;
}

.project-right-holder {
    width: 80%;
    position: absolute;
    bottom: 0;
}

.project-right-holder h1 {
    margin-left: 10px;
    text-shadow:
        0.5px 0.5px 0 #000,
        -0.5px 0.5px 0 #000,
        -0.5px -0.5px 0 #000,
        0.5px -0.5px 0 #000;
}

.project-right-holder h2 {
    margin-left: 10px;
    text-shadow:
        0.5px 0.5px 0 #000,
        -0.5px 0.5px 0 #000,
        -0.5px -0.5px 0 #000,
        0.5px -0.5px 0 #000;
}

.project-right-technologies .skills-table {
    margin-bottom: 10px;
}


.project-right-technologies .skills-table th p,
.project-right-technologies .skills-table td p {
    display: flex;
    align-items: center;
    padding: 0px;
    margin: 2px;
    width: 10vw;
    text-overflow: ellipsis;
    font-size: 0.7vw;
    padding: 5px;
}

.project-right-technologies .skill-icon {
    width: 1.5vw;
    height: 1.5vw;
    margin: 0;
    border-radius: 50%;
    background: white;
    margin-right: 10px;
}

.project-right-technologies .skills-table th p {
    color: #ffffff;
    text-shadow:
        1px 1px 0 #000,
        -1px 1px 0 #000,
        -1px -1px 0 #000,
        1px -1px 0 #000;
}

.basic-info {
    font-size: 1vw;
    text-shadow:
        1px 1px 0 #ff2e5b,
        -1px 1px 0 #ff2e5b;
    color: #ffffff;
}



/* -------------- Я не знаю чё это -----------*/

.project-gallery-container {
    padding: 2rem;
}

.slider-wrapper {
    position: relative;
    max-width: 48rem;
    margin: 0 auto;
}

.slider {
    display: flex;
    aspect-ratio: 16/9;
    overflow-x: auto;
    scroll-snap-type: x mandatory;
    scroll-behavior: smooth;
    box-shadow: 0 1.5rem 3rem -0.75rem hsla(0, 0%, 0%, 0.25);
}

.slider img {
    flex: 1 0 100%;
    scroll-snap-align: start;
    object-fit: cover;
}

.slider-nav {
    position: absolute;
    color: white;
    display: flex;
    bottom: 0;
    left: 0;
}

.slider-nav a {
    border-radius: 50%;
    background-color: black;
    opacity: 0.75;
    transition: opacity ease 250ms;
}

.slider-nav a:hover {
    opacity: 1;
}

/* ----------  TITLE + DESCRIPTION ------------ */

.project-description {
    padding-left: 5vw;
    padding-right: 5vw;
    color: white;
    font-size: 3vh;
}

.project-description-title {
    padding-right: 35vw;
    font-weight: 300;
}

.project-description-title h1 {
    color: white;
    font-size: 56px;
    font-weight: 700;
    background: linear-gradient(to right, rgb(255, 255, 255), rgb(255, 255, 255), rgb(255, 220, 234), rgb(255, 157, 203), rgb(255, 203, 223), rgb(255, 255, 255), rgb(255, 255, 255));
    background-clip: text;
    color: transparent;
    filter: drop-shadow(0px 0px 15px #ff2e5b);
    background-size: 200%;
    animation: gradientAnimation 5s linear infinite;
}

.project-description-info {
    border-top: 3px solid rgb(70, 70, 70);
    display: flex;
}

.project-description-info-section {
    width: 30vw;
    align-items: center;
    display: flex;
    flex-direction: column;
}

.project-description-info-section img {
    width: 3vw;
    height: auto;
    background-color: rgb(255, 255, 255);
    border-radius: 50%;
    margin-top: 30px;
    filter: invert(100%);
}

.project-description-info-section h2 {
    margin: 10px;
    font-size: 1vw;
}

.project-description-info-section p {
    margin: 0;
    font-size: 0.95vw;
    color: #7b7b7b;
}

/* ------------ FEATURES -----------*/
.feature-block {
    margin: 0px 5vw;
    margin-bottom: 200px;
    margin-top: -1vw;
    height: auto;
    font-size: 1vw;
    color: white;
}

.feature-block h1 {
    font-size: 2vw;
    font-family: "Montserrat", sans-serif;
}

.feature-points {
    list-style-type: none;
}

.feature-point {
    position: relative;
    margin-left: 2vw;
}

.feature-point h2 {
    margin: 0;
    padding: 1vw;
    font-weight: 200;
}


.feature-point::before {
    content: '';
    position: absolute;
    left: -1vw;
    top: 1.4vw;
    width: 20px;
    height: 20px;
    background-image: url(/public/icons/point.png);
    filter: invert(100%);
    background-size: contain;
    background-repeat: no-repeat;
    margin-left: -20px;
    z-index: 1;
}

.feature-point::after {
    content: '';
    position: absolute;
    left: -1vw;
    top: 1.5vw;
    width: 20px;
    height: 100%;
    background-image: url(/public/icons/line.png);
    filter: invert(100%);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    margin-left: -20px;
}

.last::after {
    background-image: none;
    background-image: url(/public/icons/endline.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}


/* ----------- COLLABORATION -------------*/

.collaboration {
    position: relative;
    margin-left: 20vw;
    margin-right: 20vw;
    margin-bottom: 20vh;
    background-color: #121212;
    padding: 3vw;
    border-radius: 60px;
    transition: 0.5s;
}

.collaboration:hover {
    background: #ff2e5b;
}

.collaboration:hover span {
    color: white;
}

.collaboration:hover button {
    background: white;
}

.collaboration h1 {
    margin-top: 0;
    font-size: 3vw;
    color: white;
}

.collaboration p {
    font-size: 1.5vw;
    color: white;
    margin-bottom: 0;
}

.collaboration span {
    font-size: 3vw;
    font-weight: 700;
    background: linear-gradient(to right, #ff2e5b, #ff2e5b, #ff2e5b, rgb(255, 141, 194), #ff2e5b, #ff2e5b, #ff2e5b);
    background-clip: text;
    color: transparent;
    background-size: 200%;
    animation: gradientAnimation 5s linear infinite;
    transition: 0.5s;
}

.collaboration button {
    position: absolute;
    justify-content: center;
    align-items: center;
    display: flex;
    right: 2vw;
    top: 2vw;
    background: #ff2e5b;
    border-radius: 50%;
    width: 5vw;
    height: 5vw;
    transition: 0.5s;
}

.collaboration img {
    position: absolute;
    width: 7vw;
    height: 7vw;
}




