.carousel {
    position: relative;
    width: 60vw;
    height: 40vw;
    perspective: 500px;
    transform-style: preserve-3d;
    display: flex;
}

.card-container {
    position: absolute;
    width: 100%;
    height: 100%;
    transform:
        rotateY(calc(var(--offset) * 50deg)) scaleY(calc(1 + var(--abs-offset) * -0.4)) translateZ(calc(var(--abs-offset) * -30rem)) translateX(calc(var(--direction) * -5rem));
    filter: blur(calc(var(--abs-offset) * 1rem));
    transition: all 0.3s ease-out;
}

@media (min-width: 1800px) {

    /* Стиль для экранов шириной 768 пикселей и более */
    .card-container {
        transform:
            rotateY(calc(var(--offset) * 50deg)) scaleY(calc(1 + var(--abs-offset) * -0.4)) translateZ(calc(var(--abs-offset) * -50rem)) translateX(calc(var(--direction) * -5rem));
    }
}

.card {
    height: 100%;
    background-color: hsl(280deg, 40%, calc(100% - var(--abs-offset) * 50%));
    background: rgb(0, 0, 0);
    border-radius: 1rem;
    color: gray;
    text-align: justify;
    transition: all 0.3s ease-out;
    border: 2px solid rgb(109, 109, 109);
    border-radius: 40px;
}

.card h2 {
    text-align: center;
    font-weight: bold;
    color: rgb(255, 255, 255);
}

.card p,
.card h2 {
    transition: all 0.3s ease-out;
    opacity: var(--active);
}

.card img {
    height: 100%;
    width: 100%;
    border-radius: 40px;
}

.nav {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 40%;
    z-index: 20;
    cursor: pointer;
    user-select: none;
    background: unset;
    border: unset;
    scale: 0.4;
    filter: drop-shadow(0px 0px 10px white) drop-shadow(0px 0px 3px white) invert(100%);

    &.left {
        left: 0;
        transform: translateX(-200%) rotate(90deg);
    }

    &.right {
        right: 0;
        transform: translateX(210%)rotate(-90deg);
    }
}


/* ------------------------- BUTTON -------------------------------*/
.primary-button {
    height: 5vh;
    width: 25vw;
    font-family: 'Ropa Sans', sans-serif;
    color: white;
    cursor: pointer;
    font-size: 1vw;
    font-weight: bold;
    letter-spacing: 0.05rem;
    border: 1px solid #000000;
    padding: 0rem 1rem;
    background-image: url("/public/images/buttonGreen.svg");
    background-color: #181818;
    background-size: 200%;
    background-position: 200%;
    background-repeat: no-repeat;
    transition: 0.6s ease-in-out;
    transition-property: background-position, border, color;
    position: relative;
    text-align: left;
    display: flex;
    align-items: center;
    z-index: 1;
}

.primary-button:hover {
    border: 1px solid #000000;
    color: rgb(0, 0, 0);
    background-position: 40%;
}

.primary-button:before {
    content: "";
    position: absolute;
    background-color: #000000;
    width: 0.2rem;
    height: 0.2rem;
    top: -1px;
    left: -1px;
    transition: background-color 0.15s ease-in-out;
}

.primary-button:hover:before {
    background-color: white;
}

.primary-button:hover:after {
    background-color: white;
}

.primary-button:after {
    content: "";
    position: absolute;
    background-color: #7aff60;
    width: 0.3rem;
    height: 0.3rem;
    bottom: -1px;
    right: -1px;
    transition: background-color 0.15s ease-in-out;
}

.button-borders {
    margin: 0px 0.5vw;
    margin-bottom: 30px;
    margin-top: 15px;
    position: relative;
    width: fit-content;
    height: fit-content;
}

.button-borders:before {
    content: "";
    position: absolute;
    width: calc(100% + 0.5em);
    height: 50%;
    left: -0.3em;
    top: -0.3em;
    border: 1px solid #000000;
    border-bottom: 0px;
    /* opacity: 0.3; */
}

.button-borders:after {
    content: "";
    position: absolute;
    width: calc(100% + 0.5em);
    height: 50%;
    left: -0.3em;
    bottom: -0.3em;
    border: 1px solid #000000;
    border-top: 0px;
    z-index: 0;
}

.shape {
    fill: #000000;
}

.primary-button img {
    margin-right: 10px;
    filter: invert(100%);
    width: 2vw;
    height: 2vw;
}

.primary-button:hover img {
    filter: invert(0%);
}

.primary-button p {
    position: absolute;
    right: 20px;
}


