.image-gallery {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.image-grid {
    margin-left: 5vw;
    margin-right: 5vw;
    display: flex;
    flex-wrap: wrap;
    max-height: calc(1px * var(--visible-rows));
    overflow: hidden;
}

.gallery-image {
    width: auto;
    height: 300px;
    border-radius: 16px;
    margin: 10px;
    cursor: pointer;
}

.show-more-button {
    background: #000000;
    --color: #ffffff;
    font-family: "Funnel Display", sans-serif;
    display: inline-block;
    width: 12em;
    height: 3.6em;
    line-height: 3.5em;
    margin: 20px;
    position: relative;
    cursor: pointer;
    overflow: hidden;
    border: 2px solid var(--color);
    transition: color 0.5s;
    z-index: 1;
    font-size: 20px;
    border-radius: 6px;
    font-weight: 600;
    color: var(--color);
}

.show-more-button:before {
    content: "";
    position: absolute;
    z-index: -1;
    background: #ff2e5b;
    height: 300px;
    width: 450px;
    border-radius: 50%;
}

.show-more-button:hover {
    color: #ffffff;
}

.show-more-button:before {
    top: 100%;
    left: 100%;
    transition: all 0.7s;
}

.show-more-button:hover:before {
    top: -60px;
    left: -60px;
}

.show-more-button:active:before {
    background: #ff2e5b;
}

.more-to-explore {
    position: relative;
    font-weight: 500;
    font-size: 27px;
    right: 35%;
    color: white;
    background: #000000;
    padding: 10px;
    border-radius: 20px;
    filter: drop-shadow(0px 0px 10px #ff2e5b);
}