/* ---- HEADER ----*/
header {
  position: fixed;
  margin-left: 3%;
  margin-right: 3%;
  padding-left: 2%;
  padding-right: 2%;
  border-radius: 0px 0px 40px 40px;
  border: 3px solid rgb(0, 0, 0);
  border-top: none;
  font-family: "Funnel Sans", sans-serif;
  width: 90vw;
  z-index: 100;
  backdrop-filter: blur(16px);
}

.navigation {
  display: flex;
  justify-content: space-between;
  height: 100px;
  align-items: center;
}

.navigation.mobile {
  justify-content: space-between
}

.menu {
  color: white;
  height: 100px;
  width: 100%;
  align-content: center;
}

header a {
  font-size: 25px;
  font-weight: 500;
  margin-right: 3%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

header a:hover {
  color: #ff2e5b;
  filter: drop-shadow(0px 0px 20px #000000);
}

.header-buttons {
  display: flex;
  gap: 20px;
}

.header-buttons-mobile {
  display: flex;
  justify-content: space-around;
}

.header-buttons-mobile.social {
  gap: 5vw;
}

.button {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  border: none;
  background: none;
  color: #ff2e5b;
  cursor: pointer;
  position: relative;
  margin-bottom: 20px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 14px;
  transition: all .15s ease;
}

.button::before,
.button::after {
  content: '';
  display: block;
  position: absolute;
  right: 0;
  left: 0;
  height: calc(50% - 5px);
  border: 1px solid #ff2e5b;
  transition: all .15s ease;
}

.button::before {
  top: 0;
  border-bottom-width: 0;
}

.button::after {
  bottom: 0;
  border-top-width: 0;
}

.button:active,
.button:focus {
  outline: none;
}

.button:active::before,
.button:active::after {
  right: 3px;
  left: 3px;
}

.button:active::before {
  top: 3px;
}

.button:active::after {
  bottom: 3px;
}

.button_lg {
  position: relative;
  display: block;
  padding: 10px 20px;
  color: #fff;
  background-color: #ff2e5b;
  overflow: hidden;
  box-shadow: inset 0px 0px 0px 1px transparent;
}

.button_lg::before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 2px;
  height: 2px;
  background-color: #ff2e5b;
}

.button_lg::after {
  content: '';
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
  width: 4px;
  height: 4px;
  background-color: #ff2e5b;
  transition: all .2s ease;
}

.button_sl {
  display: block;
  position: absolute;
  top: 0;
  bottom: -1px;
  left: -8px;
  width: 0;
  background-color: #ff4655;
  transform: skew(-15deg);
  transition: all .2s ease;
}

.button_text {
  position: relative;
}

.button:hover {
  color: #0f1923;
}

.button:hover .button_sl {
  width: calc(100% + 15px);
}

.button:hover .button_lg::after {
  background-color: #ff2e5b;
}

.icon {
  height: 55px;
  width: 55px;
  border-radius: 50%;
  background: rgb(255, 255, 255);
  cursor: pointer;
  transition: 0.3s;
  margin-right: 2%;
  border: 2px solid black;
}

.icon-mobile {
  margin-right: 0;
  height: 50px;
  width: 50px;
}

.icon-menu {
  content: url(/public/icons/menu.png);
}

.telegram {
  content: url(/public/icons/tg.png);
  filter: invert(100%);
}

.git {
  content: url(/public/icons/git.png);
  filter: invert(100%);
  border: none;
}

.in {
  content: url(/public/icons/in.png);
  filter: invert(100%);
}



.icon:hover {
  filter: drop-shadow(0px 0px 20px #ff2e5b)
}

.switch {
  width: 135px;
  height: 55px;
  border-radius: 55px;
  background-color: rgb(187, 187, 187);
  overflow: hidden;
}

.theme {
  margin: 2px;
  width: 51px;
  height: 51px;
  border-radius: 55px;
  background-color: black;
  filter: invert();
  transition: 0.5s ease-in-out;
}

.theme.light {
  content: url(/public/icons/icon_small.png);
}

.switch:hover>.theme.theme.light {
  transform: rotate(45deg);
}

.contactButton {
  background: #000000;
  margin-right: 2%;
  height: 55px;
  width: 300px;
  padding: 0em 1em;
  border: none;
  border-radius: 20px;
  font-weight: bold;
  letter-spacing: 3px;
  text-transform: uppercase;
  cursor: pointer;
  color: #ffffff;
  transition: all 1000ms;
  font-size: 20px;
  overflow: hidden;
  position: relative;
  outline: 2px solid #ffffff;
}

.contactButton.mobile {
  margin: 30px;
  margin-left: 0;
}

.contactButton:hover {
  color: #ffffff;
  transform: scale(1.1);
  outline: 2px solid #ff2e5b;
  box-shadow: 4px 5px 17px -4px #ff2e5b;
}

.contactButton::before {
  content: "";
  position: absolute;
  left: -50px;
  top: 0;
  width: 0;
  height: 100%;
  background-color: #ff2e5b;
  transform: skewX(45deg);
  z-index: -1;
  transition: width 1000ms;
}

.contactButton:hover::before {
  width: 250%;
}


