/* ------- WELCOME BLOCK --------- */
.welcome-block {
  margin-left: 5%;
  margin-right: 5%;
  position: relative;
  font-family: "Montserrat", sans-serif;
  height: 700px;
  padding-top: 200px;
  padding-bottom: 350px;
  display: flex;
  place-items: center;
  color: white;
}

.welcome-block.mobile {
  padding-top: 150vw;
  width: auto;
}

.typewrite-block.mobile {
  height: 50vw;
}

.welcome-block-background {
  content: "";
  position: relative;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: 0%;
  padding: 0;
  background-image: url("/public/images/back1.jpeg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  z-index: -0;
}

.welcome-block-background::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #000000d8;
  z-index: -1;
}

.welcome-block-background::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 350px;
  background: linear-gradient(to bottom, #ffffff00, #000000);
}

.welcome-block-span {
  color: rgb(255, 46, 91);
}

.welcome-block h1 {
  font-weight: 700;
  font-size: 52px;
}

.first-block.mobile h1 {
  font-weight: 700;
  font-size: 15vw;
  margin: 0;
}

.typewrite.mobile {
  font-size: 10vw;
  margin: 0;
}

.welcome-block h2 {
  font-size: 32px;
  font-weight: 450;
}

.welcome-block.mobile h2 {
  font-size: 5vw;
}

.welcome-block h3 {
  font-size: 30px;
  font-weight: 300;
}

.welcome-block.mobile h3 {
  font-size: 4vw;
}

.title {
  background: linear-gradient(to right, rgb(255, 46, 91), rgb(255, 46, 91), rgb(255, 115, 169), rgb(255, 196, 224), rgb(255, 115, 169), rgb(255, 46, 91), rgb(255, 46, 91));
  background-clip: text;
  color: transparent;
  background-size: 200%;
  animation: gradientAnimation 5s linear infinite;
}

@keyframes gradientAnimation {
  0% {
    background-position: 200% 50%
  }

  100% {
    background-position: 0% 50%
  }
}

.welcome-block .first-block {
  align-content: center;
  width: 55%;
}

.main-image-box {
  width: 45%;
}

.first-image-layer {
  margin-right: -5.5%;
  right: 0;
  top: 100px;
  margin-top: 5%;
  position: absolute;
  width: 56%;
  opacity: 0.7;
  z-index: -1;
}

.first-image-layer.mobile {
  width: 100%;
}

.second-image-layer {
  animation: bouncing 5s linear infinite;
  position: absolute;
  margin-right: 2%;
  right: 0;
  top: 100px;
  margin-top: 10%;
  height: auto;
  width: 30%;
}

.second-image-layer.mobile {
  width: 100%;
}

.third-image-layer {
  /* content: url(/public/images/custom_back1.png); */
  height: 550px;
  position: absolute;
  margin-right: -2%;
  right: 0;
  top: 100px;
  margin-top: 27%;
  position: absolute;
  width: 24%;
  height: auto;
  opacity: 1;
}

.fourth-image-layer {
  height: 550px;
  position: absolute;
  margin-left: -500px;
  margin-top: 24px;
}

.fifth-image-layer {
  height: 550px;
  position: absolute;
  margin-left: -550px;
  margin-top: 24px;
}

@keyframes bouncing {
  0% {
    transform: translateY(0px)
  }

  50% {
    transform: translateY(20px)
  }

  0% {
    transform: translateY(0px)
  }
}

.wrap {
  color: rgb(255, 46, 91);
  font-family: "Noto Sans Mono", monospace;
  font-weight: 400;
}

