  margin: 200px 5%;
  height: auto;
  font-size: 26px;
  color: white;
  border-top: 4px solid rgb(79, 79, 79);
}

.experience-block h1 {
  font-size: 50px;
  font-family: "Montserrat", sans-serif;
}

.experience-block h2 {
  margin-bottom: 0;
}

.experience-block.mobile h2 {
  font-size: 20px;
}

.experience-block h3 {
  margin: 0;
  margin-top: 10px;
  font-size: 24px;
  font-weight: 600;
}

.experience-block.mobile h3 {
  font-size: 15px;
}

.experience-block p {
  margin-bottom: 10px;
  margin-top: 20px;
  font-size: 25px;
}

.experience-block.mobile p {
  font-size: 12px;
}

.experience-block.mobile li {
  font-size: 12px;
}

.experience-date {
  font-size: 20px;
  font-weight: 300;
  color: #ffffff97;
}

.experience-icon {
  width: 100px;
  height: 100px;
  margin: 40px 20px;
  border-radius: 20px;
}

.experience-icon.mobile {
  margin: 40px 10px;
  height: 20vw;
}

.experience-points {
  list-style-type: none;
  padding-left: 20px;
}

.experience-points .point {
  font-family: "Montserrat", sans-serif;
  position: relative;
  padding-left: 60px;
}

.experience-points.mobile .point {
  padding-left: 20px;
}

.experience-points .point::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50px;
  width: 40px;
  height: 40px;
  background-image: url(/public/icons/point.png);
  filter: invert(100%);
  background-size: contain;
  background-repeat: no-repeat;
  margin-left: -20px;
  z-index: 1;
}

.experience-points .point::after {
  content: '';
  position: absolute;
  left: -1px;
  top: 51px;
  width: 40px;
  height: 100%;
  background-image: url(/public/icons/line.png);
  filter: invert(100%);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  margin-left: -20px;
}

.experience-points .last::after {
  background-image: url(/public/icons/endline.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.inside-points {
  margin-top: 0;
  margin-bottom: 50px;
  list-style-type: disc;
}

.inside-points li {
  padding-top: 20px;
}

.point {
  margin-top: 0px;
  display: flex;
}

