/* -------- BUTTON TO UP ----------*/
.btn-up {
  position: fixed;
  left: 2%;
  bottom: 5%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: .3s linear;
  animation: appearance 2s forwards;
  border: none;
  opacity: 0;
  transform: translateY(0px);
  background: transparent;
}

.btn-up.mobile {
  left: 2%;
  bottom: 1%;
}

@keyframes appearance {
  0% {
    opacity: 0%;
    transform: translateY(40px)
  }

  100% {
    opacity: 100%;
    transform: translateY(0px)
  }
}

.btn-up::before {
  content: "";
  height: 50px;
  width: 250px;
  background-size: 100% 100%;
  background-image: url(/public/icons/backtotop.png);
  filter: invert(100%);
}

.btn-up.mobile::before {
  width: 100px;
  height: 25px;
}