.modal-menu-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.5s ease, visibility 0.5s ease;
    z-index: 1000;
}

.modal-menu-backdrop.show {
    opacity: 1;
    visibility: visible;
}

.modal-menu-content {
    font-family: "Montserrat", sans-serif;
    color: white;
    display: flex;
    flex-direction: column;
    top: 100px;
    right: 10vw;
    position: relative;
    background: rgb(24, 24, 24);
    border-radius: 16px;
    width: 50vw;
    font-size: 7vw;
    line-height: 12vw;
    height: 73vw;
    transform: translateY(-2vh);
    transition: transform 0.5s ease;
    border: 3px solid rgb(62, 62, 62);
    padding-bottom: 0;
    z-index: 1001;
}

.modal-menu-content a {
    padding: 1vw 6vw 1vw 6vw;
    border-bottom: 0.5vw solid rgb(30, 30, 30);
}

.modal-menu-content a:last-child {
    border-bottom: none;
    margin-bottom: 0;
}

.modal-menu-backdrop.show .modal-menu-content {
    transform: translateY(0);
}

.social-button:hover {
    filter: drop-shadow(0px 0px 10px #ff2e5b);
    scale: 1.05;
    color: rgb(255, 255, 255);
}

.contact-text {
    color: white;
    font-size: 25px;
    margin-bottom: 30px;
}